export default {
	data() {
		return {
			list: [],
			self: ''
		};

	},
	onLoad() {
		this._load()
	},
	methods: {
		_load() {
			this.$api.http.get('fx/user/get_fx_rank').then(res => {
				this.list = res.data.rank
				this.self = res.data.self
			})
		}
	}
}
